* {
    margin: 0;
    padding: 0;
}
body{
    background-color: #85DCB0;
    padding: 8px;
}
.container{
    min-height: 500px;    
    background-color: #ececec;
    max-width: 500px;
    margin: 0px auto;
    padding: 5px;
    border-radius: 5px;
    box-shadow: 0px 0px 20px #299788;
}

.app-name{
    color:white;
    background-color: #41B3A3;
    text-align: center;
    font-weight: bold;
    padding: 10px;
    margin: 5px;
    margin-bottom: 10px;
    border-radius: 5px;
}

.search-bar{
    display: flex;
    justify-content: center;
    padding: 0px 5px;
    margin-top: 10px;
}

.search-bar input{
    padding: 6px;
    width: 95%;
    margin: 0px;
    border: 1px #41B3A3 solid;
    border-radius: 5px;
}


.contact-card{
    background-color: #ffffff;
    padding: 10px;
    margin: 8px 12px;
    color: #0f0f0f;
    box-shadow: 0px 0px 3px grey;
    border-radius: 5px;
}

.contact-name{
    font-size: 18px;
    margin-left: 6px;
    margin-bottom: 6px;
}

.contact-number{
    margin-left: 6px;
    font-size: 12px;
    margin-bottom: 5px;
}

.new-contact-btn{
    display: flex;
    justify-content: center;
}

.new-contact-form {
    margin-top: 10px;
}


.new-contact-form > div{
    display: flex;
    justify-content: center;
    padding: 0px 5px;
    margin: 5px 0px;
}

.new-contact-form > div input{
    padding: 6px;
    width: 95%;
    margin: 0px;
    border: 1px #41B3A3 solid;
    border-radius: 5px;
    
}
.new-contact-form > div input:focus, .search-bar input:focus{
    outline: none;
    border-color: #41B3A3;
    border-width: 2px;
}



.new-contact-btn button{
    margin: 10px 0px 5px 0px;
    width: 96%;
    padding: 5px;
    border: none;
    background-color: #25bda8;
    color: #fff;
    font-weight: bold;
    border-radius: 6px;
}

.new-contact-form .form-btns{
    padding: 5px 12px;
    display: flex;
    justify-content: space-between;
}

.new-contact-form .form-btns > button{
    padding: 5px;
    border: none;
    color:#fff;
    border-radius: 4px;
}

li{
    list-style-type: none;
}

.error-msg{
    font-size: 15px;
    color:red;   
}

.action-btns{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 6px;
    margin-bottom: 0px;
}

.contact-content{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}


button{
    padding:5px;
    border: none;
    border-radius: 5px;
    color: #fff;
}

.call-btn{
    margin-right: 6px;
    background-color: rgb(34, 182, 41);
}

.edit-btn{
    background-color: rgb(250, 196, 21);
}

.delete-btn{
    background-color: rgb(255, 72, 0);
}

#back-search-btn, #cancel-search-btn{
    display: none;
    /* width: 5%; */
    background-color: #ececec;
    color: #299788;
    font-weight: bold;
    padding: 0 5px 0 5px;
}